:root { --mc: #008850; --sc: #00e085; --lc: #f9f9f9; --dc: #444444; --gc: #dddddd; }

.inner { position: relative; width: 1200px; margin: 0 auto; }
.cbtn { display: inline-block; padding: 10px 20px; border: 1px solid var(--mc); color: var(--mc); font-size: 13px; background: var(--lc); }
.cbtn::after { content: '\e93e'; color: var(--mc); font-family: xeicon; margin-left: 10px; font-size: 10px; }

.csc { position: relative; padding: 100px 0; }
.csc h2 { font-size: 25px; font-weight: 700; margin: 0 0 20px 0; text-align: center; }
.csc p { font-size: 15px; margin: 0 0 40px 0; text-align: center; }

.TopBanner { background: var(--mc); color: var(--lc); overflow: hidden; height: 155px; }
.TopBanner.on { height: 0; transition: 0.5s; }
.TopBanner .inner { padding: 50px 0; }
.TopBanner i { position: absolute; top: 50px; right: 0; font-size: 30px; }
.TopBanner h2 { font-size: 30px; font-weight: 700; margin: 0 0 10px 0; }
.TopBanner span { font-weight: 300; color: var(--sc); }

.Header { position: sticky; top: 0; left: 0; width: 100%; z-index: 9999; }
.Header .hd_wrap { position: relative; background: rgba(0, 0, 0, 0.5); color: var(--lc); line-height: 100px; transition: 0.5s; }
.Header.on .hd_wrap { background: var(--lc); color: var(--dc); line-height: 80px; }
.Header .hd_wrap:hover { background: var(--lc); color: var(--dc); }
.Header h1 { position: absolute; top: 0; left: 70px; font-size: 0; }
.Header .hd_wrap h1 a { display: inline-block; font-size: 0; width: 188px; height: 22px; background: url(../public/assets/img/logo_w.png); vertical-align: middle; }
.Header .hd_wrap:hover h1 a { background: url(../public/assets/img/logo.png); }
.Header.on .hd_wrap h1 a { background: url(../public/assets/img/logo.png); }
.Header .top_service { position: absolute; top: 0; right: 70px; }
.Gnb>ul { display: flex; gap: 40px; }
.Header .top_service { display: flex; gap: 40px; }
.Header .top_service .project li { display: inline-block; font-weight: 300; font-size: 15px; }
.Header .top_service .project li~li::before { content: ""; display: inline-block; width: 1px; height: 10px; background: var(--gc); margin: 0 10px; }
.Header .top_service strong { cursor: pointer; }
.Header .top_service strong:hover { color: #008850; }
.Header .top_service strong.on { color: #008850; }
.Header .top_service .lang { position: relative; float: left; margin: 0 20px 0 0; }
.Header .top_service .lang .arrow { font-size: 7px; vertical-align: 0.3em; }
.Header .top_service .lang.on .arrow { transform: rotate(180deg); }
.Header .top_service .lang .lang_box { display: none; position: absolute; top: 70px; left: 50%; transform: translateX(-50%); line-height: 1.45; border: 2px solid var(--mc); background: var(--lc); color: #444; padding: 20px 35px; }
.Header .top_service .lang.on .lang_box { display: block; }
.Header .top_service .top_search { position: relative; }
.Header .top_search .search_box { display: none; position: absolute; top: 80px; right: -65px; line-height: 1; border: 1px solid var(--gc); background: var(--lc); padding: 30px 50px; width: 600px; text-align: center; }
.Header .top_search.on .search_box { display: block; }
.Header .top_search .search_box input[type="text"] { font-size: 20px; width: 450px; border: none; outline: none; border-bottom: 2px solid var(--dc); }
.Header .top_search .search_box button { background: transparent; border: none; outline: none; font-size: 30px; }
.Header .top_search .search_box::before { content: ""; position: absolute; top: -10px; right: 60px; width: 20px; height: 20px; background: var(--lc); border-left: 1px solid var(--gc); border-top: 1px solid var(--gc); transform: rotate(45deg); }

.MainVisual { position: relative; color: var(--lc); transform: translateY(-100px); height: calc(100vh - 255px); }
.MainVisual figure { position: relative; height: calc(100vh - 155px); background-repeat: no-repeat; background-position: center center; background-size: 130% auto; transition: 0.5s; }
.MainVisual figure.on { background-size: 100% auto; }
.MainVisual .itm01 { background-image: url(../public/assets/img/main_visual01.jpg); }
.MainVisual .itm02 { background-image: url(../public/assets/img/main_visual02.jpg); }
.MainVisual .itm03 { background-image: url(../public/assets/img/main_visual03.jpg); }
.MainVisual figure .inner { position: absolute; inset: 50% auto auto 50%; transform: translate(-50%, -50%); }
.MainVisual .slick-slide, .MainVisual .slick-track { transition-duration: 5ms; }
.MainVisual .slideNum { position: absolute; bottom: 100px; left: 50%; margin: 0 0 0 -600px; }
.MainVisual .slideTab { position: absolute; top: calc(50% + 75px); left: 50%; transform: translate(-100%,-50%); margin: 0 0 0 600px; }

.MainVisual .slideTab li { text-align: right; margin-bottom: 20px; font-size: 0; }
.MainVisual .slideTab li::after { content: ''; display: inline-block; width: 20px; height: 20px; border: 1px solid var(--lc); margin: 0 0 2px 10px; vertical-align: middle; }
.MainVisual .slideTab li.on::after { background: var(--lc); }
.MainVisual .slideTab li.on { font-size: 15px; }

.MainVisual .slideArrows button { position: absolute; top: calc(50% + 65px); left: 50%; transform: translate(-50%,-50%); background: transparent; border: none; color: var(--lc); font-size: 20px; padding: 30px; background: rgba(0, 0, 0, 0.1); }
.MainVisual .slideArrows button:first-child { margin: 0 0 0 -700px; }
.MainVisual .slideArrows button:last-child { margin: 0 0 0 700px; }
.MainVisual .tit { font-size: 50px; font-weight: 700; margin: 0 0 30px 0; white-space: pre-line; line-height: 1.2; }
.MainVisual .des { font-size: 20px; font-weight: 300; margin: 0 0 30px 0; }

.MainContent .inner { display: flex; }
.MainContent .inner>figure { flex: 1; text-align: center; }
.MainContent figure img { transition: 0.5s; }
.MainContent figure:hover img { transform: scale(1.2); }
.MainContent .box { margin: 0 0 40px 0; overflow: hidden; }
.MainContent .tit { position: relative; font-size: 21px; font-weight: 500; margin: 0 0 20px 0; }
.MainContent .tit::after { position: absolute; left: 50%; top: -40px; transform: translate(-50%,-50%); content: ''; width: 2px; height: 50px; background: var(--mc); }
.MainContent .dec { word-break: keep-all; font-size: 15px; margin: 0 0 30px 0; line-height: 1.45; white-space: pre-line; }

.Portfolio { background: url(../public/assets/img/section1_bg.jpg) repeat-x; }
.Portfolio h2 { padding: 40px 0 0 0; color: var(--lc); text-align: left; }
.Portfolio p { color: var(--lc); text-align: left; }
.Portfolio .arrows { display: inline-block; align-self: flex-start; border: 1px solid var(--lc); color: var(--lc); font-size: 0; }
.Portfolio .arrows i { padding: 10px; font-size: 30px; }
.Portfolio .arrows i~i { border-left: 1px solid var(--lc); }
.Portfolio .container { display: flex; width: calc(1200px + (100% - 1200px)/2); margin: 0 0 0 auto; }
.Portfolio .container>div { min-width: 0; }
.Portfolio .container .left { width: 450px; }
.Portfolio .container .right { flex: 1; }
.Portfolio .right_slide figure { padding: 0 20px 0 0; }
.Portfolio .right { display: flex; flex-direction: column; }
.Portfolio .right .slide { overflow: hidden; margin: auto 0 0 0; }
.Portfolio .right_slide { width: 1300px; }
.Portfolio .box { margin: 0 0 30px 0; }
.Portfolio .des { font-size: 20px; font-weight: 500; }
.Portfolio .left .box { width: 400px; box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2); }

.Solution .solution_slide figure { width: 1200px; }
.Solution .solution_slide figure img { margin: 0 auto; }
.Solution .container { position: relative; }
.Solution .box { position: absolute; top: 100px; left: 50%; margin: 0 0 0 -540px; width: 300px; box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2); background: #fff; padding: 50px 40px; }
.Solution .box::after { position: absolute; top: 60px; left: -20px; content: ''; width: 40px; height: 2px; background: var(--mc); }
.Solution .box .tit { font-size: 25px; font-weight: 500; margin: 0 0 20px 0; }
.Solution .box p { font-size: 15px; font-weight: 300; min-height: 150px; text-align: left; line-height: 1.45; word-break: keep-all; }
.Solution .box .content { margin: 0 0 40px 0; }
.Solution .num { font-size: 30px; font-weight: 500; color: var(--mc); }
.Solution .num span { font-size: 20px; font-weight: 300; color: var(--dc); }
.Solution .arrows i { position: absolute; inset: 40% auto auto 50%; transform: translate(-50%,-50%); font-size: 20px; color: var(--lc); padding: 20px 40px; background: rgba(0, 0, 0, 0.2); cursor: pointer; }
.Solution .arrows i:first-child { margin: 0 0 0 -630px; }
.Solution .arrows i:last-child { margin: 0 0 0 630px; }
.Solution .dots { text-align: center; font-size: 0; }
.Solution .dots>li { display: inline-block; width: 20px; height: 20px; background: var(--gc); margin: 30px 10px; }
.Solution .dots>li.on { background: var(--mc); }


